import React from 'react'
import './Part2.css';

const Part2 = () => {
    return (
        <div className="Part2">

         <h2> Diseñar una Web es un arte  </h2>

            <p> En la actualidad, un negocio que no se encuentra en Internet es como si no existiera, así de simple. Por eso es vital que tu empresa este presente en Internet y tenga una página web, para que tus clientes o futuros clientes estén siempre informados. 
                    
                <br/><br/>
                Si aún no tienes una página web, te damos algunas razones por las que deberías tener  una:
            
                <br/><br/>


            1# Presenta y representa a tu empresa
            Una web es el escaparate perfecto para captar futuros clientes, tanto si vendes productos como servicios. Es la primera imagen que el cliente se lleva de tu negocio. Está demostrado que un cliente que está comparando varias empresas, escogerá a la empresa que tiene presencia online ya que transmite más prestigio y calidad. 

            <br/><br/>

            2# Atención al cliente
            El tener una web te brinda la oportunidad de estar en contacto constante con tus clientes, que pueden consultarte en cualquier momento o ver tus productos y/o servicios a cualquier hora. Eso les da confianza y seguridad, y también nos dará una buena imagen responder los opiniones de estos clientes.<br/><br/>

            3# Información y posición
            Actualmente, el mayor porcentaje de búsqueda de profesionales se hace en Internet. Cuando la gente realiza esas búsquedas en la red, es importante que nos encuentre en los primereos puestos, por eso la importancia de un buen trabajo de posicionamiento SEO.
            
            <br/><br/>


            4# FAQ, y ahorra tiempo
            Seguro que más de una vez te han mandado el mismo email o llamado al negocio dos clientes distintos pero con la misma pregunta, ¿verdad? Con una web puedes crear una sección FAQ (Preguntas frecuentes) donde puedas resolver este tipo de dudas y ahorrar tiempo.
            
            <br/><br/>

            5# Excelente relación coste-beneficio

            Una web tiene un coste bajo comparado con otros medios publicitarios y una vez hecha la inversión la acabas amortizando en poco tiempo.

            <br/> <br/>

            6# Llega a un público más amplio
            Con una web tienes posibilidades de ampliar fronteras y captar clientes de otras ciudades, países e incluso continentes. 

            <br/> <br/> 
                        
            También resulta útil para fidelizar a los clientes que ya tienes, ofreciéndoles ventajas o entreteniéndoles con contenidos de interés. Tus clientes también podrán recomendarte más fácilmente, a través de email o redes sociales.

            </p>

        </div>

    )
}

export default Part2
