import React from 'react'
import './Nav.css';
import logo from '../../images/logo.svg';
      
const Nav = () => {
  return (
    
      

   
      <div className="Nav">
      
      <header className="Nav-header">
      
 
        <div id="navbar">
        <a href="#home">info</a>
        <a href="#aboutus"> info</a>
        <a href="#contactos">info</a>
        <a href="#pago">info</a>
        <img src={logo} className="logo" alt="logo" />
      </div>
  
      </header>




    </div>
  )
}

export default Nav

